@import "parts/_vars.scss";
@import "parts/_header.scss";
// @import "parts/_header1.scss";
@import "parts/_font-face.scss";
@import "parts/_footer.scss";
// @import "parts/_palette";
// @import "parts/_slick-slider";
@import "parts/_works.scss";
// @import "parts/_libs.scss";
@import "parts/page-blog";
@import "parts/page-catalog";

.page {
  font-size: 10px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Базовые стили */
.body {
  // font-family: 'Open Sans', sans-serif;
  font-size: 1.5rem;
  line-height: 2.3rem;
  color: $light-theme__main-text;
  background-color: $light-theme__main-bg;

  display: grid;
  // grid-template-rows: min-content auto min-content;
  grid-template-rows: 1fr min-content;
  height: 100vh;
  overflow-x: hidden;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  margin: 0 auto;
  max-width: 1760px;
  padding: 0 15px;
}

.container--inner {
  padding: 0 20px;
}

/* M A I N */
.main {
  margin-top: 65px;
  .heading {
    margin-bottom: 50px;
    text-align: center;
    font-size: 4.2rem;
    line-height: 1.2;
  }

  .block-title {
    margin-bottom: 20px;
    margin-top: 30px;
    color: #757575;
    font-size: 2.8rem;
    line-height: 1.2;
  }

  // Адаптив
  @media all and (max-width: 767px) {
    .heading {
      font-size: 2.5rem;
      margin-bottom: 30px;
    }

    .body {
      font-size: 1.3rem;
      line-height: 2rem;
    }
    .block-title {
      font-size: 1.8rem;
      line-height: 1.2;
    }

  }

  .text__link {
    text-decoration: underline;
    margin-bottom: 5px;
    display: inline-block;
  }

  .text__link:hover,
  .text__link:focus,
  .text__link:active {
    text-decoration: none;
  }


  .breadcumbs {
    font-size: 12px;
    display: flex;
    flex-wrap: wrap;
    column-gap: 25px;
    margin-left: 10px;
    margin-bottom: 15px;
  }

  .breadcrumbs-item {
    position: relative;
    text-decoration: underline;
  }

  .breadcrumbs-item:hover {
    text-decoration: none;
  }

  .breadcrumbs-item::before {
    content: "";
    position: absolute;
    width: 6px;
    height: 6px;
    background-color: $light-theme__main-text;
    left: -10px;
    top: 8px;
    transform: rotate(45deg);
  }

  .breadcumbs .breadcrumbs-item:last-of-type {
    opacity: 0.5;
    text-decoration: none;
  }
}

// D a r k  -  t h e m e
.dark-theme {
  background-color: $dark-theme__main-bg;
  color: $dark-theme__main-text;

  .header {
    background-color: $dark-theme__header-bg;
    color: $dark-theme__header-text;
    &__menu {
      background-color: $dark-theme__header-bg;
    }

    &__menu-item:hover,
    &__menu-item:focus,
    &__menu-item:active {
      background-color: $dark-hover;
      color: $dark-theme__header-bg;
      opacity: 0.8;
    }

    &__menu-item--active::before {
      background-color: $dark-theme__header-text;
    }

    &__btn>span {
      background-color: $dark-theme__header-text;
    }

    &__logo {
      box-shadow: 0 0 10px 0 white,
        0 0 100px 0 white inset;
      opacity: 0.8;

      &:hover {
        opacity: 1;
      }
    }
  }

  .footer {
    background-color: $dark-theme__footer-bg;

    &__social-link:hover svg,
    &__social-link:active svg,
    &__social-link:focus svg {
      color: $hover;
      fill: $hover;
    }

    svg {
      fill: #908a8a;
    }
  }
}