.catalog {
  .section {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));

    &__heading {
      margin-bottom: 30px;
      margin-top: 20px;
      color: #757575;
      font-size: 2.8rem;
      line-height: 1.2;
    }

    &__list {
      padding-left: 15px;
    }

    &__item {
      margin-bottom: 10px;
    }

    &__link {
      padding: 5px 0;
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }
  }


  &__item {
    margin-bottom: 10px;
  }

  &__item-link {
    padding: 5px;
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }
}