.works {
display: grid;
// grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
gap: 20px;

  &__list {
    display: flex;
    column-gap: 25px;
    flex-wrap: wrap;
  }

  &__item {
    margin-bottom: 10px;
  }

  &__link {
    display: inline-block;
    position: relative;
    border: 2px solid #757575;
    padding: 5px 10px;
    border-radius: 5px;
    opacity: 0.7;
  }

  &__link:hover {
    opacity: 1;
  }
  @media all and (max-width: 767px) {
  
  .works__list {
    column-gap: 15px;
  }
  .works__link {
    font-size: 1.3rem;
    line-height: 1.2;
  }
}
}
