/* F O O T E R */
.footer {
  font-size: 2rem;
  background-color: $light-theme__footer-bg;
  margin-top: 50px;

  &__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin: 15px 0;
    column-gap: 15px;
  }

  &__contacts {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;

    a {
      text-decoration: underline;
      line-height: 1.3;
    }

    a:hover,
    a:active,
    a:focus {
      text-decoration: none;
      opacity: 1;
    }
  }

  &__social {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 50px;
    margin-left: 30px;
  }

  &__social-link {
    position: relative;
    cursor: pointer;
    padding: 15px 0;

    svg {
      position: absolute;
      top: 18px;
      left: -30px;
    }

    .svg-git {
      top: 16px;
    }

    .svg-vk {
      top: 17px;
    }
  }


  &__social-link:hover,
  &__social-link:active,
  &__social-link:focus,
  &__social-link:hover svg,
  &__social-link:active svg,
  &__social-link:focus svg {
    color: $hover;
    fill: $hover;
  }

  &__copy {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
  }

  &__copy-link {
    border: 1px solid $light-theme__main-text;
    color: $light-theme__main-text;
    padding: 5px 10px;
    margin-top: 5px;
    color: inherit;
    border-color: inherit;
  }

  &__copy-link:hover {
    border: 1px solid $hover;
    color: $hover;
  }
}



@media all and (max-width: 830px) {
  .footer {
    font-size: 1.1rem;

    &__social {
      gap: 30px;
      margin-left: 0;
      padding: 10px 10px 0 10px;
    }

    &__social-link {
      margin-right: 10px;

      svg {
        width: 30px;
        height: 30px;
        top: 0px;
        left: -10px;
      }

      .svg-git {
        width: 35px;
        height: 35px;
        top: -2px;
      }

      .svg-vk {
        width: 32px;
        height: 32px;
        top: -1px;
      }

      span {
        display: none;
      }
    }

    &__contacts {
      display: flex;
    }

    &__contacts-mail {
      margin-bottom: 5px;
    }

    &__contacts-tel {
      margin-bottom: 5px;
    }

    &__copy-link {
      padding: 0px 5px;
      margin-top: 0;
    }
  }
}