// Актуальный хэдер
.header {
  // background-color: #2757dd;
  background-color: $light-theme__header-bg;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 1.6rem;
  color: $light-theme__header-text;
  min-height: 53px;
  position: fixed;
  width: 100%;
  z-index: 10;
  box-shadow: 0px 0px 4px $light-theme__main-text;

  .container.header__container {
    display: flex;
    width: 100%;
    justify-content: space-between;
  }

  &__menu {
    display: flex;
    justify-content: end;
    flex-wrap: wrap;
    column-gap: 25px;
    transition: right .4s;
    background-color: $light-theme__header-bg;
    // box-shadow: 0px 0px 4px $light-theme__main-text;
    // z-index: 2;
  }

  &__menu-item {
    padding: 19px 5px;
    font-weight: 600;
  }

  &__menu-item:hover,
  &__menu-item:focus, 
  &__menu-item:active {
    background-color: $hover;
    color: $light-theme__header-bg;
    opacity: 0.8;
  }

  &__menu-item--active {
    position: relative;
    cursor: default;
  }

  &__menu-item--active::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: $light-theme__header-text;
    bottom: 0;
    left: 0;
    right: 0;
  }

  &__btn-area {
    padding: 10px 10px 10px 10px;
    margin-right: -10px;
    z-index: 11;
    display: none;
    cursor: pointer;
  }

  &__btn {
    width: 30px;
    height: 20px;
    position: relative;
    transition: transform .4s;
  }

  &__btn>span {
    display: block;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    background-color: $light-theme__header-text;
    transition: transform .4s;
  }

  &__btn>span:nth-child(2) {
    transform: translateY(-50%);
    top: 50%;
  }

  &__btn>span:nth-child(3) {
    transform: translateY(-100%);
    top: 100%;
  }

  &__btn.show>span:nth-child(2) {
    display: none;
  }

  &__btn.show>span:nth-child(1) {
    transform: translateY(500%) rotate(135deg);
  }

  &__btn.show>span:nth-child(3) {
    transform: translateY(-500%) rotate(-135deg);
  }

  &__logo {
    display: inline-block;
    background-color: transparent;
    border-radius: 50%;
    cursor: pointer;
    align-self: flex-start;
    margin: 5px 0;
    opacity: 0.8;
    box-shadow: 0 0 10px 0 #212121,
      0 0 100px 0 #212121 inset;
  }

  &__logo:hover,
  &__logo:active,
  &__logo:focus {
    opacity: 1;
  }
}




















@media (max-width: 768px) {

  .header__menu {
    flex-direction: column;
    position: absolute;
    // background-color: inherit;
    width: 50%;
    right: -60%;
    top: 0;
    box-shadow: 0px 0px 4px $light-theme__main-text;
  }

  .header__menu.show {
    right: 0;
  }

  .header__btn-area {
    display: block;
  }


  .header__menu.header__menu--show {
    right: 0;
  }

  .header__menu-item {
    padding: 15px 15px;
  }

  .header__menu-item--active {
    position: relative;
  }

  .header__menu-item--active::before {
    content: '';
    position: absolute;
    width: 4px;
    height: 100%;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .header__logo {
    align-self: center;
  }
  .header__logo-img {
    width: 35px;
    height: 30px;
  }

}